import Vue from 'vue'
import Router from 'vue-router'


import Login from './../components/Login'//
import Basis from './../components/Basis'//
import Main from './../components/Main'//
import Download from './../components/Download'//
import About from './../components/About'//

import NotFound from './../components/views/404'//404

import Home from './../components/views/Home'//
import Business from './../components/views/Business'//
import Setting from './../components/views/Setting'//
import Service from './../components/views/Service'//
import Mall from './../components/views/Mall'//
import Learn from './../components/views/Learn'//
import DataAndPwd from './../components/views/DataAndPwd'//

import UpdPwd from './../components/views/DataAndPwd/UpdPwd'//
import MyData from './../components/views/DataAndPwd/MyData'//

import UpdMsg from './../components/views/MyData/UpdMsg'//
import UpdIcon from './../components/views/MyData/UpdIcon'//

import Organization_List from './../components/views/Setting/Organization_List'//
import Organization_Settings from './../components/views/Setting/Organization_Settings'//
import Administrator_List from './../components/views/Setting/Administrator_List'//
import File_List from '../components/views/Setting/File_List'//
import Department_List from './../components/views/Setting/Department_List'//
import Personnel_List from './../components/views/Setting/Personnel_List'//
import Tag_List from './../components/views/Setting/Tag_List'//
import AddDepartment from './../components/views/Setting/AddDepartment'//
import AddPersonnel from '../components/views/Setting/AddPersonnel'//
import AddTag from '../components/views/Setting/AddTag'//

import Exam_List from './../components/views/business/Exam_List'//
import AddExam from './../components/views/business/AddExam'//
import UpdExam from './../components/views/business/UpdExam'//
import Paper_List from './../components/views/business/Paper_List'//
import AddPaper from './../components/views/business/AddPaper'//
import UpdPaper from './../components/views/business/UpdPaper'//
import Test_List from './../components/views/business/Test_List'//
import Topic_List from './../components/views/business/Topic_List'//
import Statistic_List from './../components/views/business/Statistic_List'//
import Exam_Statistic from './../components/views/business/Exam_Statistic'//
import Exam_Person from './../components/views/business/Exam_Person'//
import Teacher_List from './../components/views/business/Teacher_List'//
import AddTeacher from './../components/views/business/AddTeacher'//
import Resource_List from './../components/views/business/Resource_List'//
import AddResource from '../components/views/business/AddResource'//
import UpdResource from '../components/views/business/UpdResource'//
import FirstP_List from './../components/views/business/FirstP_List'//
import AddCourseSort from './../components/views/business/AddCourseSort'//
import AddCourseSortT from './../components/views/business/AddCourseSortT'//
import UpdCourseSort from './../components/views/business/UpdCourseSort'//

import Chapter from './../components/views/business/Chapter'//
import Exam_Result from './../components/views/business/Exam_Result'//--

import Plate from './../components/views/business/FirstP_List/Plate'//
import Carousel from './../components/views/business/FirstP_List/Carousel'//
import AddCarousel from './../components/views/business/FirstP_List/AddCarousel'//
import UpdCarousel from './../components/views/business/FirstP_List/UpdCarousel'//
import Advert from './../components/views/business/FirstP_List/Advert'//
import AddAdvert from './../components/views/business/FirstP_List/AddAdvert'//
import UpdAdvert from './../components/views/business/FirstP_List/UpdAdvert'//


import Course from './../components/views/business/Resource_List/Course'//
import CourseSort from './../components/views/business/Resource_List/CourseSort'//

Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/Login',
      component: Login
    },
    {
      path: '/',
      component: Basis,
      children: [
        {
          path: '/Home',
          component: Home
        },
        {
          path: '/Business',
          component: Business,
          children: [
            {
              path: '/FirstP_List',
              component: FirstP_List,
              children:[
                {
                  path: '/Plate',
                  component: Plate
                },
                {
                  path: '/Carousel',
                  component: Carousel
                },
                {
                  path: '/AddCarousel',
                  component: AddCarousel
                },
                {
                  path: '/UpdCarousel',
                  component: UpdCarousel
                },
                {
                  path: '/Advert',
                  component: Advert
                },
                {
                  path: '/AddAdvert',
                  component: AddAdvert
                },
                {
                  path: '/UpdAdvert',
                  component: UpdAdvert
                }
              ]
            },
            {
              path: '/Topic_List',
              component: Topic_List,
            },
            {
              path: '/Exam_List',
              component: Exam_List,
            },
            {
              path: '/AddExam',
              component: AddExam,
            },
            {
              path: '/UpdExam',
              component: UpdExam,
            },
            {
              path: '/Paper_List',
              component: Paper_List,
            },
            {
              path: '/AddPaper',
              component: AddPaper,
            },
            {
              path: '/UpdPaper',
              component: UpdPaper,
            },
            {
              path: '/Test_Detail',
              component: Topic_List,
            },
            {
              path: '/Test_List',
              component: Test_List,
            },
            {
              path: '/Statistic_List',
              component: Statistic_List,
            },
            {
              path: '/Exam_Statistic',
              component: Exam_Statistic,
            },
            {
              path: '/Exam_Person',
              component: Exam_Person,
            },
            {
              path: '/Teacher_List',
              component: Teacher_List,
            },
            {
              path: '/AddTeacher',
              component: AddTeacher,
            },
            {
              path: '/Chapter',
              component: Chapter
            },
            {
              path: '/Exam_Result',
              component: Exam_Result
            },
            {
              path: '/AddCourseSort',
              component: AddCourseSort
            },
            {
              path: '/AddCourseSortT',
              component: AddCourseSortT
            },
            {
              path: '/UpdCourseSort',
              component: UpdCourseSort
            },
            {
              path: '/Resource_List',
              component: Resource_List,
              children: [
                {
                  path: '/Course',
                  component: Course
                },
                {
                  path: '/CourseSort',
                  component: CourseSort
                },
                {
                  path: '/AddResource',
                  component: AddResource,
                },
                {
                  path: '/UpdResource',
                  component: UpdResource,
                }
              ]
            }
          ]
        },
        {
          path: '/Setting',
          component: Setting,
          children: [
            {
              path: '/Organization_List',
              component: Organization_List
            },
            {
              path: '/Organization_Settings',
              component: Organization_Settings
            },
            {
              path: '/Administrator_List',
              component: Administrator_List
            },
            {
              path: '/File_List',
              component: File_List
            },
            {
              path: '/AddDepartment',
              component: AddDepartment
            },
            {
              path: '/Department_List',
              component: Department_List
            },
            {
              path: '/Personnel_List',
              component: Personnel_List
            },
            {
              path: '/AddPersonnel',
              component: AddPersonnel
            },
            {
              path: '/Tag_List',
              component: Tag_List
            },
            {
              path: '/AddTag',
              component: AddTag
            },
          ]
        },
        {
          path: '/Service',
          component: Service
        },
        {
          path: '/Mall',
          component: Mall
        },
        {
          path: '/Learn',
          component: Learn
        },
        {
          path: '/DataAndPwd',
          component: DataAndPwd,
          children:[
            {
              path: '/MyData',
              component: MyData,
              children: [
                {
                  path: '/UpdMsg',
                  component: UpdMsg,
                },
                {
                  path: '/UpdIcon',
                  component: UpdIcon,
                }
              ]
            },
            {
              path: '/UpdPwd',
              component: UpdPwd
            },
          ]
        }
      ]
    },
    {
      path: '*',
      component: NotFound
    },
    {
      path: '/download',
      component: Download,
    },
    {
      path: '/about',
      component: About,
    },
  ]
})



