      <el-dialog :visible.sync="dialogVisible3" title="" size="tiny" style="min-width:1100px;">
        <div style="height:150px;">
          <div><el-radio v-model="radio" label="3"><span style="padding-left:10px">(MP3MP4wavmepg)</span></el-radio></div>
          <div style="margin-top:10px"><el-radio v-model="radio" label="2"><span style="padding-left:10px">(docxpptpdftxthtml)</span></el-radio></div>
          <div style="margin-top:10px"><el-radio v-model="radio" label="1"><span style="padding-left:10px">(jpgpngjepgbmpgif)</span></el-radio></div>
          <div style="margin-top:10px"><el-radio v-model="radio" label="6"><span style="padding-left:10px">(rarzip)</span></el-radio></div>
