<script>
import { getattach,addattach,updattach,delattach,changeattach,UploadFile,getattachforname,Separated,Transcode,Removefile,Getfile} from '../../../api/api';
import NotContent from '../Modules/NotContent';
import { formatDate } from '../../../util/util';
export default {
  data() {
      return {
        searchTxt:'',
        tofdis:[],
        // Gloading:true,
        loading:true,
        loading2:false,
        dialogVisible: false,
        dialogVisible2: false,
        dialogVisible3:false,
        dialogVisible4:false,
        uploadTxt: '',
        tofAddfold:false,
        tofAddfold2:false,
        tofAddfoldTxt:'',
        tofAddfoldTxt2:'',
        dropdown:true,
        dropdownname:false,
        open:true,
        choeseF:[],
        choeseT:'',
        // tofChangeAttach:'',
        qwe:0,
        tableData: [],
        filepath: [
          {
            parent:0,
            fileName:''
          }
        ],
        outsideurl:'',
        NotDis:false,
        uploaddata:'',
        radio:'1',
        filename:'',
        Separated:'',
        SeparateId:'',
        SeparatedTof:true,
        btnradio: false,
        hackReset: false,
        percentage: 0,
        tofIE9:navigator.appName == "Microsoft Internet Explorer"&&parseInt(navigator.appVersion.split(";")[1].replace(/[ ]/g, "").replace("MSIE",""))<=9
      }
    },
    beforeUpdate:function(){
      // this.openError(window.screen.availHeight-200);
    },
    components:{
        NotContent
    },
    beforeCreate:function(){
      if(window.localStorage.getItem('saasLoginTrueOrFalse')==='false' || window.localStorage.getItem('saasLoginTrueOrFalse')===null){
        this.$router.push('/Login');
      }else{
          var loginParams = { parent: 0};
          getattach(loginParams).then(data =>{
            // console.log(data);
            if(data.success){
              if(data.resList.length!==0){
                this.NotDis = false;
                this.tableData = data.resList;
                for(var i=0;i<this.tableData.length;i++){
                  var date = new Date(this.tableData[i].updateTime);
                  this.tableData[i].tofdis = false;
                  this.tableData[i].updatetime = formatDate(date, 'yyyy-MM-dd hh:mm');
                }
              }else{
                this.NotDis = true;
              }
              this.loading = false;
              this.hack();
            }else{
              this.openError(data.resMsg);
            }
          });
        }
    },
    methods:{
      openSuccess(text) {
        this.$message({
          message: text,
          type: 'success'
        });
      },
      openError(text) {
        this.$message({
          showClose: true,
          message: text,
          type: 'error'
        });
      },
      hack(){
        this.hackReset = false; // hack
        this.$nextTick(() => {
          this.hackReset = true;
        })
      },
      addoutsideurl(){
        this.dialogVisible2 = true;
        this.outsideurl = '';
        setTimeout(function() {
            document.querySelector('#outsidename input').focus();
            // console.log(document.querySelector('#outsidename input'));
        }, 20);
      },
      //
      Onaddoutsideurl(){
        var urllist = this.outsideurl.split('/');
        var url = '';
        if(urllist[urllist.length-1]===''){
          url = urllist[urllist.length-2];
        }else{
          url = urllist[urllist.length-1];
        }
        // console.log(url);
        var loginParams = { parent: this.filepath[this.filepath.length-1].parent,name:url,type:4,url:this.outsideurl};
        addattach(loginParams).then(data =>{
        this.loading = false;
        this.dialogVisible2 = false;
        if(data.success){
          var loginParams = { parent: this.filepath[this.filepath.length-1].parent};
          getattach(loginParams).then(data =>{
            if(data.success){
              this.tableData = data.resList;
              if(this.tableData.length!==0){
                this.NotDis = false;
                for(var i=0;i<this.tableData.length;i++){
                  var date = new Date(this.tableData[i].updateTime);
                  this.tableData[i].tofdis = false;
                  this.tableData[i].updatetime = formatDate(date, 'yyyy-MM-dd hh:mm');
                }
              }else{
                if(this.filepath[this.filepath.length-1].parent===0){
                  this.NotDis = true;
                }
              }
            }else{
              this.openError(data.resMsg);
            }
          })
          }else{
            this.openError(data.resMsg);
          }
        })
      },
      //
      gopath(id,name,type){
        if(type===5){
          // this.openError(id+"--"+name+"-"+type);
          var loginParams = { parent: id};
          getattach(loginParams).then(data =>{
            // console.log(data);
          if(data.success){
            this.filepath[this.filepath.length] = {
              parent: id,
              fileName: name
            }
            this.tableData = data.resList;
            if(this.tableData!==undefined){
              for(var i=0;i<this.tableData.length;i++){
                var date = new Date(this.tableData[i].updateTime);
                this.tableData[i].tofdis = false;
                this.tableData[i].updatetime = formatDate(date, 'yyyy-MM-dd hh:mm');
              }
            }
          }else{
            this.openError(data.resMsg);
          }
        })
        }
      },
      //
      backpath(id){
        var temp = [];
        for(var i=0;i<this.filepath.length;i++){
          if(this.filepath[i].parent!==id){
            temp[i] = this.filepath[i];
          }else{
            temp[i] = this.filepath[i];
            this.filepath = [];
            this.filepath = temp;
            break;
          }
        }

        var loginParams = { parent: id};
        getattach(loginParams).then(data =>{
          if(data.success){
            this.tableData = data.resList;
            if(data.resList.length!==0){
              this.NotDis = false;
              for(var i=0;i<this.tableData.length;i++){
                var date = new Date(this.tableData[i].updateTime);
                this.tableData[i].tofdis = false;
                this.tableData[i].updatetime = formatDate(date, 'yyyy-MM-dd hh:mm');
              }
            }else{
              if(id===0){
                this.NotDis = true;
              }
            }
          }else{
            this.openError(data.resMsg);
          }
        })
        this.hack();
      },
      back1(){
        if(this.filepath.length>1){
          var id = this.filepath[this.filepath.length-2].parent;
          this.backpath(id);
        }
      },
      //
      addfold(){
        this.tofAddfold = true;
        setTimeout(function() {
          document.getElementById('myinput').focus();
        }, 20);
      },
      //
      addfold2(){
        this.tofAddfold2 = true;
        setTimeout(function() {
          document.getElementById('myinput2').focus();
        }, 20);
      },
      //
      OnAttach(){
        var loginParams = { parent: this.filepath[this.filepath.length-1].parent,name:this.tofAddfoldTxt,type:5};
        this.tofAddfold = false;
        this.tofAddfoldTxt = '';
        if(loginParams.name!==''){
          addattach(loginParams).then(data =>{
            if(data.success){
              var loginParams = { parent: this.filepath[this.filepath.length-1].parent};
              getattach(loginParams).then(data =>{
                if(data.success){
                  this.tableData = data.resList;
                  if(this.tableData.length!==0){
                    this.NotDis = false;
                    for(var i=0;i<this.tableData.length;i++){
                      var date = new Date(this.tableData[i].updateTime);
                      this.tableData[i].tofdis = false;
                      this.tableData[i].updatetime = formatDate(date, 'yyyy-MM-dd hh:mm');
                    }
                  }else{
                    this.NotDis = true;
                  }
                }else{
                  this.openError(data.resMsg);
                }
              })
            }else{
              this.openError(data.resMsg);
            }
          })
        }
      },
      //
      OnAttach2(){
        var loginParams = { parent: this.filepath[this.filepath.length-1].parent,name:this.tofAddfoldTxt2,type:5};
        this.tofAddfold2 = false;
        this.tofAddfoldTxt2 = '';
        if(loginParams.name!==''){
          addattach(loginParams).then(data =>{
            if(data.success){
              var loginParams = { parent: this.filepath[this.filepath.length-1].parent};
              getattach(loginParams).then(data =>{
              if(data.success){
                this.tableData = data.resList;
                if(this.tableData.length!==0){
                  this.NotDis = false;
                  for(var i=0;i<this.tableData.length;i++){
                    var date = new Date(this.tableData[i].updateTime);
                    this.tableData[i].tofdis = false;
                    this.tableData[i].updatetime = formatDate(date, 'yyyy-MM-dd hh:mm');
                  }
                }else{
                  this.NotDis = true;
                }
              }else{
                this.openError(data.resMsg);
              }
            })
            }else{
              this.openError(data.resMsg);
            }
          })
        }
      },
      //
      handleCommand(command){
        // this.$message('click on item ' + command);
        var obj = document.getElementsByName("Fruit");
        var check_val = [];
        for(var i = 0;i<obj.length;i++){
            if(obj[i].checked)
              check_val.push(obj[i].value);
        }
        if(command==='a'){
          this.choeseF = [];
          // this.choeseF = check_val;
          for(var i in check_val){
            this.choeseF[i] = parseInt(check_val[i]);
          }
          this.choeseT = '';
          // console.log(this.choeseF);
          // console.log(this.choeseT);
          this.dialogVisible = true;
        }else if(command==='b'){
          this.choeseF = [];
          this.choeseF = check_val;
          this.choeseT = '';
          // console.log(this.choeseF);
          // console.log(this.choeseT);
          this.dialogVisible = true;
        }else if(command==='c'){
          for(var i=0;i<this.tableData.length;i++){
            if(this.tableData[i].id === parseInt(check_val[0])){
            // console.log(parseInt(check_val[0]));
              this.tableData[i].tofdis = true;
              this.qwe++;
              setTimeout(function() {
              var id = parseInt(check_val[0]);
                document.getElementById(id+'t').focus();
              }, 200);
            }
          }
        }else if(command==='d'){
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
            }).then(() => {
              if(check_val!==null){
                for(var i=0;i<check_val.length;i++){
                  check_val[i] = parseInt(check_val[i]);
                }
                var loginParams = { attachId: check_val};
                delattach(loginParams).then(data =>{
                  if(data.success){
                    if(this.filepath.length>0){
                      var id = this.filepath[this.filepath.length-1].parent;
                      this.backpath(id);
                      this.hack();
                    }
                  }else{
                    this.openError(data.resMsg);
                  }
                })
              }
            }).catch(() => {
          });
        }else if(command==='e'){
          this.choeseF = [];
          this.choeseF = check_val;
          this.radio = '0';
          this.dialogVisible3 = true;
        }else if(command==='f'){
          // this.loading = true;
          for(var i=0;i<this.tableData.length;i++){
            if(this.tableData[i].id === this.SeparateId){
              this.tableData[i].btnradio = true;
              this.tableData[i].transcode = 2;
            }
          }
          Transcode(this.Separated).then(data =>{
            // console.log('===================');
            // console.log(this.Separated);
            // console.log(data);
            var data = JSON.parse(data);
            if(data.success){
              var loginParams = { id: this.SeparateId, audioUrl:data.url, transcode: 0};
              changeattach(loginParams).then(data2 =>{
                this.loading = false;
                if(data2.success){
                  this.openSuccess('');
                  var loginParams2 = { parent: this.filepath[this.filepath.length-1].parent};
                  getattach(loginParams2).then(data3 =>{
                    if(data3.success){
                      this.tableData = data3.resList;
                      if(data3.resList.length!==0){
                        this.NotDis = false;
                        for(var i=0;i<this.tableData.length;i++){
                          var date = new Date(this.tableData[i].updateTime);
                          this.tableData[i].tofdis = false;
                          this.tableData[i].updatetime = formatDate(date, 'yyyy-MM-dd hh:mm');
                        }
                      }else{
                        if(id===0){
                          this.NotDis = true;
                        }
                      }
                    }else{
                      this.openError(data.resMsg);
                    }
                  });
                  this.hack();
                }else{
                  this.openError(data2.resMsg);
                }
              })
            }else if(!data.success){
              if(data.url===''){
                // this.loading = false;
                this.openError('');
              }else{
                var loginParams = { id:this.SeparateId, audioUrl:data.url, min:data.min};
                changeattach(loginParams).then(data =>{
                  this.loading = false;
                  if(data.success){
                    this.openSuccess('');
                  }else{
                    this.openError(data.resMsg);
                  }
                })
              }
            }else{
              // this.loading = false;
              this.openError('');
            }
          });
        }
      },
      //
      changename(id,name){
        var loginParams = { id: id,name:name};
        changeattach(loginParams).then(data =>{
          if(data.success){
            var id = this.filepath[this.filepath.length-1].parent;
          }else{
            this.openError(data.resMsg);
          }
            this.backpath(id);
            this.qwe++;
        })
      },
      //
      openurl(){
        var obj = document.getElementsByName("Fruit");
        var check_val = [];
        for(var k in obj){
              if(obj[k].checked)
                  check_val.push(parseInt(obj[k].value));
          }
        for(var i=0;i<this.tableData.length;i++){
          if(this.tableData[i].id===check_val[0]){
            // console.log(this.tableData[i].url);
            window.open(this.tableData[i].url);
          }
        }
      },
      //
      Changedropdown(){
        var obj = document.getElementsByName("Fruit");
        var check_val = [];
          // for(var k in obj){
          //     if(obj[k].checked)
          //     // console.log(k);
          //         check_val.push(parseInt(obj[k].value));
          // }
          for(var k=0;k<obj.length;k++){
            if(obj[k].checked){
              check_val.push(parseInt(obj[k].value));
            }
          }
          // console.log(check_val);
          if(check_val.length===1){
            for(var i in this.tableData){
              if(this.tableData[i].id===check_val[0]){
                if(this.tableData[i].type!==5){
                  this.open = false;
                }
              }
            }
          }else{
            this.open = true;
          }
          if(check_val.length>0){
            this.dropdown = false;
            if(check_val.length===1){
              for(var k in this.tableData){
                if(this.tableData[k].id===check_val[0]){
                  var namelist = this.tableData[k].name.split('.');
                  var name = namelist[namelist.length-1];
                  // console.log(name);
                  if(this.tableData[k].type===3&&name!=='mp3'){
                    this.SeparateId = this.tableData[k].id;
                    this.SeparatedTof = false;
                    if(this.tableData[k].audioUrl!==undefined&&this.tableData[k].audioUrl!==''){
                      this.SeparatedTof = true;
                    }else{
                      this.SeparatedTof = false;
                    }
                    var Surl = this.tableData[k].url.split(':8080/');
                    // this.Separated = '/home/austin/www/' + Surl[1];
                    this.Separated = '/home/ubuntu/www/' + Surl[1];
                    // console.log(this.tableData[k].url);
                  }
                }
              }
              this.dropdownname = false;
            }else{
              this.SeparatedTof = true;
              this.dropdownname = true;
            }
          }else{
            this.dropdown = true;
          }
      },
      //
      CopyOrCut(){
        var loginParams = { parent: this.filepath[this.filepath.length-1].parent,attachidList:this.choeseF};
        if(this.choeseT===''){
          // console.log(loginParams);
          addattach(loginParams).then(data =>{
            if(data.success){
                var loginParams = { parent: this.filepath[this.filepath.length-1].parent};
                getattach(loginParams).then(data =>{
                if(data.success){
                  this.tableData = data.resList;
                  if(this.tableData.length!==0){
                    this.NotDis = false;
                    for(var i=0;i<this.tableData.length;i++){
                      var date = new Date(this.tableData[i].updateTime);
                      this.tableData[i].tofdis = false;
                      this.tableData[i].updatetime = formatDate(date, 'yyyy-MM-dd hh:mm');
                    }
                  }else{
                    this.NotDis = true;
                  }
                }else{
                  this.openError(data.resMsg);
                }
              })
              }else{
                this.openError(data.resMsg);
              }
          })
        }else{
          // console.log(loginParams);
          changeattach(loginParams).then(data =>{
            if(data.success){
                var loginParams = { parent: this.filepath[this.filepath.length-1].parent};
                getattach(loginParams).then(data =>{
                if(data.success){
                  this.tableData = data.resList;
                  if(this.tableData.length!==0){
                    this.NotDis = false;
                    for(var i=0;i<this.tableData.length;i++){
                      var date = new Date(this.tableData[i].updateTime);
                      this.tableData[i].tofdis = false;
                      this.tableData[i].updatetime = formatDate(date, 'yyyy-MM-dd hh:mm');
                    }
                  }else{
                    this.NotDis = true;
                  }
                }else{
                  this.openError(data.resMsg);
                }
              })
              }else{
                this.openError(data.resMsg);
              }
          })
        }
      },
      //
      show(ev){
        if(ev.keyCode===13){
          this.getsearchTxt();
        }
      },
      //
      getsearchTxt(){
        var loginParams = { name: this.searchTxt,parent:this.filepath[this.filepath.length-1].parent};
        getattachforname(loginParams).then(data =>{
          if(data.success){
            this.tableData = data.resList;
                if(this.tableData!==undefined){
                  for(var i=0;i<this.tableData.length;i++){
                    var date = new Date(this.tableData[i].updateTime);
                    this.tableData[i].tofdis = false;
                    this.tableData[i].updatetime = formatDate(date, 'yyyy-MM-dd hh:mm');
                  }
                }
          }else{
            this.openError(data.resMsg);
          }
        });
      },
      //
      getcourses(){
        if(this.searchTxt===''){
          this.getsearchTxt();
        }
      },
      //
      Onaddattach(){
        if(this.radio!=='0'){
          var loginParams = { type: this.radio,attachidList:this.choeseF};
          // console.log(loginParams);
          changeattach(loginParams).then(data =>{
            if(data.success){
                var loginParams = { parent: this.filepath[this.filepath.length-1].parent};
                getattach(loginParams).then(data =>{
                  if(data.success){
                    this.dialogVisible3 = false;
                    this.openSuccess('');
                    this.tableData = data.resList;
                    if(this.tableData.length!==0){
                      this.NotDis = false;
                      for(var i=0;i<this.tableData.length;i++){
                        var date = new Date(this.tableData[i].updateTime);
                        this.tableData[i].tofdis = false;
                        this.tableData[i].updatetime = formatDate(date, 'yyyy-MM-dd hh:mm');
                      }
                    }else{
                      this.NotDis = true;
                    }
                  }else{
                    this.openError(data.resMsg);
                  }
                })
              }else{
                this.openError(data.resMsg);
              }
          })
        }
        // console.log(this.uploaddata);
      },
      //
      successRes(data){
        console.log('============successRes===========');
        console.log(data);
        var data = JSON.parse(data);
        this.uploaddata = data;
        // if(data.resMsg === "success"){
        if(data.success){
          this.loading2 = false;
          var name = data.filename.split('.');
          if(name.length>1){
            var tname = name[name.length-1];
            if(tname==='mp3'||tname==='mp4'||tname==='wav'||tname==='mepg'){
              this.radio = '3';
            }else if(tname==='docx'||tname==='ppt'||tname==='pdf'||tname==='txt'||tname==='html'){
              this.radio = '2';
            }else if(tname==='jpg'||tname==='png'||tname==='jepg'||tname==='bmp'||tname==='gif'){
              this.radio = '1';
            }else{
              this.radio = '6';
            }
          }else{
            this.radio = '6';
          }

          // this.uploaddata.url: /home/ubuntu/www/vod/2018/6/27/1530084934019260.mp4
          var temp = this.uploaddata.url.split('www/'), fileurl;
          if(temp.length>1) fileurl = this.Host+':8080/'+temp[1];
          else fileurl = this.Host+':8080/'+this.uploaddata.url;

          var loginParams = '';
          if(tname==='mp3'){
            loginParams = { parent: this.filepath[this.filepath.length-1].parent,name:this.uploaddata.filename,type:this.radio,audioUrl:fileurl,url:fileurl};
          } else {
            if(data.Separate!==undefined){
              if(data.Separate==='false'){
                this.openError('');
                loginParams = { parent: this.filepath[this.filepath.length-1].parent,name:this.uploaddata.filename,type:this.radio,url:fileurl};
              } else {
                var namelist = this.uploaddata.url.split('.');
                var SeparateName = namelist[0];
                for(var i=1;i<namelist.length-1;i++){
                  SeparateName = SeparateName +'.'+ namelist[i]
                }
                loginParams = { parent: this.filepath[this.filepath.length-1].parent,name:this.uploaddata.filename,type:this.radio,audioUrl:SeparateName+'.mp3',url:fileurl,min:this.uploaddata.min};
              }
            }else{
              loginParams = { parent: this.filepath[this.filepath.length-1].parent,name:this.uploaddata.filename,type:this.radio,url:fileurl};
            }
          }

          this.loading = false;
          // var loginParams = { parent: this.filepath[this.filepath.length-1].parent,name:this.uploaddata.filename,type:this.radio,url:this.uploaddata.url};
          addattach(loginParams).then(data1 =>{
            if(data1.success){
              var loginParams = { parent: this.filepath[this.filepath.length-1].parent};
              getattach(loginParams).then(data2 =>{
                if(data2.success){
                  this.tableData = data2.resList;
                  if(this.tableData.length!==0){
                    this.NotDis = false;
                    for(var i=0;i<this.tableData.length;i++){
                      var date = new Date(this.tableData[i].updateTime);
                      this.tableData[i].tofdis = false;
                      this.tableData[i].updatetime = formatDate(date, 'yyyy-MM-dd hh:mm');

                      if(this.tableData[i].id === data1.id && tname=='mp4'){
                        this.tableData[i].btnradio = true;
                        this.tableData[i].transcode = 2;
                      }
                    }

                    if(tname=='mp4'){
                      // Add
                      // console.log('===========Transcode============');
                      Transcode(this.uploaddata.url).then(res=>{
                        // console.log(res);
                        if(res.resCode===0){
                          this.openSuccess('');
                          Removefile({}).then(res2=>{});    //  /tmp/upload_*

                          var id = data1.id;
                          // var audioUrl = this.Host+':8080'+res.path.split('.')[0].split('www')[1]+'.mp3';
                          var audioUrl = res.audioUrl;
                          Getfile(this.uploaddata.url).then(data3=>{
                            if(data3.resCode == 0){
                              var size = data3.size;
                              var loginParams2 = { parent: this.filepath[this.filepath.length-1].parent, id:id,audioUrl:audioUrl,url:fileurl, size:size, transcode:0};
                              updattach(loginParams2).then(data4=>{
                                if(data4.resCode===0){
                                  var loginParams3 = { parent: this.filepath[this.filepath.length-1].parent};
                                  getattach(loginParams3).then(data5 =>{
                                    if(data5.success){
                                      this.tableData = data5.resList;
                                      if(data5.resList.length!==0){
                                        this.NotDis = false;
                                        for(var i=0;i<this.tableData.length;i++){
                                          var date = new Date(this.tableData[i].updateTime);
                                          this.tableData[i].tofdis = false;
                                          this.tableData[i].updatetime = formatDate(date, 'yyyy-MM-dd hh:mm');
                                        }
                                      }else{
                                        if(id===0){
                                          this.NotDis = true;
                                        }
                                      }
                                    }else{
                                      this.openError(data5.resMsg);
                                    }
                                    this.hack();
                                  });
                                }
                              });
                            }
                          });
                        }
                      });
                    }
                    else{
                      // mp4
                      // console.log(this.uploaddata.url);
                      Getfile(this.uploaddata.url).then(data3=>{
                        if(data3.resCode == 0){
                          var size = data3.size, id = data1.id;
                          var loginParams2 = { parent: this.filepath[this.filepath.length-1].parent, id:id, url:fileurl, size:size};
                          updattach(loginParams2).then(data4=>{
                            if(data4.resCode===0){
                              var loginParams3 = { parent: this.filepath[this.filepath.length-1].parent};
                              getattach(loginParams3).then(data5 =>{
                                if(data5.success){
                                  this.tableData = data5.resList;
                                  if(data5.resList.length!==0){
                                    this.NotDis = false;
                                    for(var i=0;i<this.tableData.length;i++){
                                      var date = new Date(this.tableData[i].updateTime);
                                      this.tableData[i].tofdis = false;
                                      this.tableData[i].updatetime = formatDate(date, 'yyyy-MM-dd hh:mm');
                                    }
                                  }else{
                                    if(id===0){
                                      this.NotDis = true;
                                    }
                                  }
                                }else{
                                  this.openError(data5.resMsg);
                                }
                                this.hack();
                              });
                            }
                          });
                        }
                      });
                    }
                  }else{
                    if(this.filepath[this.filepath.length-1].parent===0){
                      this.NotDis = true;
                    }
                  }
                }else{
                  this.openError(data2.resMsg);
                }
              })
            }else{
              this.openError(data1.resMsg);
            }
          })
        }else{
          this.openError(data.resMsg);
        }
      },
      //
      OnUploadFile2(e){
        //IE9
        this.filename = '';
        if(this.tofIE9){
          var obj = document.getElementById("locafile2");
          obj.select(); obj.blur();
          var filess = document.selection.createRange().text;
          var namelist = filess.split("\\");
          this.filename = namelist[namelist.length-1]
          // console.log(this.filename);
        }
        var that = this;
        that.loading2 = true;
        var options = {
          url:that.Url,
          type:'post',
          data:that.filename,
          enctype:'multipart/form-data',
          error:function(err){
            // console.log(err);
            that.loading2 = false;
            that.openError('');
          },
          success:that.successRes
        }
        that.Jq("#form2").ajaxSubmit(options);

        //inputinputchange
        if (window.navigator.userAgent.indexOf("MSIE")>=1){
          this.Jq('#locafile2').select();
          document.execCommand('Delete');
        }else{
          this.Jq('#locafile2').val('')
        }
      },
      //
      OnUploadFile(e){
        // var FormData = new FormData();
        //IE9
        this.filename = '';
        if(this.tofIE9){
          var obj = document.getElementById("locafile1");
          obj.select(); obj.blur();
          var filess = document.selection.createRange().text;
          var namelist = filess.split("\\");
          this.filename = namelist[namelist.length-1]
          // console.log(this.filename);
        }

        var that = this;
        console.log('=======this.Url=========');
        console.log(this.Url);
        that.dialogVisible4 = true;
        that.uploadTxt = '...';
        that.percentage = 0;
        // that.loading = true;
        var options = {
          url:that.Url,
          type:'post',
          enctype:'multipart/form-data',
          uploadProgress: function(event, position, total, percentComplete) {//
            that.percentage = percentComplete;
            if(percentComplete == 100){
              // percentComplete position total 
              that.uploadTxt = '';
              // console.log(percentComplete + '%', position, total, that.uploadTxt);
              setTimeout(function(){
                that.dialogVisible4 = false;
              }, 2000);
            }
          },
          error:function(err){
            that.loading = false;
            that.openError('');
          },
          success:that.successRes
        }
        that.Jq("#form").ajaxSubmit(options);
        //inputinputchange
        if (window.navigator.userAgent.indexOf("MSIE")>=1){
          this.Jq('#locafile1').select();
          document.execCommand('Delete');
        }else{
          this.Jq('#locafile1').val('')
        }
        return false;
      }
    }
}
</script>



